<template>
  <div>
    <div class="search-box mt20">
      <div class="field">
        <label>申请人</label>
        <el-select v-model="searchObj.proposer" placeholder="请选择">
          <el-option
            v-for="item in proposerList"
            :key="item"
            :label="item"
            :value="item">
          </el-option>
        </el-select>
        <!-- <el-input v-model.trim="searchObj.proposer" placeholder="请输入用户ID/手机号" class="width200"></el-input> -->
      </div>
      <div class="field">
        <label>审核人</label>
        <el-select v-model="searchObj.auditor" placeholder="请选择">
          <el-option
            v-for="item in auditorList"
            :key="item"
            :label="item"
            :value="item">
          </el-option>
        </el-select>
        <!-- <el-input v-model.trim="searchObj.auditor" placeholder="请输入用户ID/手机号" class="width200"></el-input> -->
      </div>
      <div class="field">
        <label>申请日期</label>
        <el-date-picker
          v-model="searchObj.proposedTime"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd">
        </el-date-picker>
      </div>
      <div class="field">
        <label>审核日期</label>
        <el-date-picker
          v-model="searchObj.auditTime"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          value-format="yyyy-MM-dd">
        </el-date-picker>
      </div>
      <div class="field">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
    </div>
    <div class="">
      <el-radio-group v-model="searchObj.status" @change="search">
        <el-radio-button :label=item.value v-for="item in whiteAuthStatus" :key="item.value">{{item.label}}</el-radio-button>
      </el-radio-group>
    </div>
    <el-table
      class="mt10"
      border
      :data="listData"
      v-loading="loading">
      <el-table-column prop="title" label="申请人"></el-table-column>
      <el-table-column prop="created_at" label="申请时间"></el-table-column>
      <el-table-column prop="audit_at" label="审核时间"></el-table-column>
      <el-table-column prop="auditor" label="审核人"></el-table-column>
      <el-table-column prop="status" label="审核结果"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="doOperate(scope.row, item)" v-for="item in (scope.row.operateList || [])" :key="item.operate_status">{{item.label}}</el-button>
          <el-button type="text" size="small" @click="doDetail(scope.row)">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pag-block" v-if="listData.length> 0">
      <div class="fl">每页{{pageSize}}条，共 {{total}} 条记录 第 {{page}}/{{total}} 页</div>
      <el-pagination
        class="fr"
				@current-change="getListbyPage"
			  :current-page="page"
			  background
			  layout="prev, pager, next, jumper"
			  :page-size="pageSize"
			  :total="total">
			</el-pagination>
    </div>
    <!-- 审核详情 -->
    <el-dialog
      title="审核详情"
      :visible.sync="dialogVisible"
      width="800px">
      <el-table
        border
        :data="detailTable">
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="uid" label="ID"></el-table-column>
        <el-table-column prop="title" label="职务说明"></el-table-column>
        <el-table-column prop="rule_type" label="开通级别"></el-table-column>
        <el-table-column prop="user_type" label="用户类型"></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex';
import validate from '@/assets/js/validate'
import typesOfRepresent from '@/assets/js/typesOfRepresent'

export default {
  components: {

  },
  watch: {

  },
  data() {
    return {
      listData: [],
      loading: false,
      page: 1,
      pageSize: 10,
      total: 0,
      whiteAuthStatus: typesOfRepresent.whiteAuthStatus,
      auditorList: [],
      proposerList: [],
      searchObj: {
        auditTime: null,
        proposedTime: null,
        status: 0,              // 申请状态，0全部，1未审核，2审核通过，3已拒绝，4已取消
        audit_start: null,      // 审核开始时间
        audit_end: null,        // 审核结束时间
        proposed_start: null,   // 申请开始时间
        proposed_end: null,     // 申请结束时间
        proposer: null,         // 申请人
        auditor: null           // 审核人
      },
      dialogVisible: false,
      detailTable: []
    };
  },
  computed: {
    ...mapGetters(['getUserInfo'])
  },
  created() {
    this.getWhiteParams();
    this.getList();
  },
  mounted() {

  },
  methods: {
    // 获取查询参数
    getWhiteParams() {
      this.$axios.get(api.whiteParamsApi, { params: {}
			}).then(res => {
				this.auditorList = res.data.result.auditor;
				this.proposerList = res.data.result.proposer;
			}).catch(err=> {
        console.log(err)
      });
    },
    getList() {
      this.loading = true;
      let params = {
        audit_start: this.searchObj.auditTime? this.searchObj.auditTime[0]: null,
        audit_end: this.searchObj.auditTime? this.searchObj.auditTime[1]: null,
        proposed_start: this.searchObj.proposedTime? this.searchObj.proposedTime[0]: null,
        proposed_end: this.searchObj.proposedTime? this.searchObj.proposedTime[1]: null,
        status: this.searchObj.status,
        proposer: this.searchObj.proposer,
        auditor: this.searchObj.auditor
      }
			this.$axios.get(api.whiteAuditListApi, { params: {
					page: this.page -1,
          page_size: this.pageSize,
          ...params
				}
			}).then(res => {
				this.loading = false;
				this.listData = ((res.data.result || {}).list) || []
				this.total = Number((res.data.result || {}).total || 0)
			}).catch(err=> {
        this.loading = false;
        console.log(err)
      });
    },
    search() {
      this.page = 1;
      this.getList();
    },
    reset() {
      this.searchObj = Object.assign(this.searchObj, {
        auditTime: null,
        proposedTime: null,            
        audit_start: null,      
        audit_end: null,        
        proposed_start: null,   
        proposed_end: null,     
        proposer: null,         
        auditor: null           
      });
    },
    doOperate(row, item){
      // 撤回取消操作不提示
      if (item.operate_status === 4 || (item.operate_status === 1)) {
        this.operateAuth({
          apply_seq: row.apply_seq,
          status: item.operate_status,
          auditor: this.getUserInfo.name
        })
        return
      }
      this.$confirm(`是否确认${item.label}申请，${item.label}后${item.operate_status === 2? '立即生效，不可撤回': '可以撤回'}`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.operateAuth({
          apply_seq: row.apply_seq,
          status: item.operate_status,
          auditor: this.getUserInfo.name
        })
      }).catch((err) => {
        console.log(err)
      });
    },
    // 1未审核（撤回），2审核通过， 3审核不通过, 4取消
    operateAuth(params) {
      this.loading = true;
      this.$axios.post(api.whiteAuditApi, params).then(res => {
				this.loading = false;
        if (res.data.errorNo && res.data.errorMsg) {
          let a = res.data.errorMsg.replace(/,/g, "<br/>")
          this.$message.warning({
            dangerouslyUseHTMLString: true,
              message: `<pre>${a}</pre>`
          });
        } else {
          this.$message.success('操作成功！');
        }
        this.getList();
			});
    },
    doDetail(row) {
      this.detailTable = [];
      this.dialogVisible = true;
      this.$axios.get(api.whiteDetailApi, {params: {apply_seq: row.apply_seq}}).then(res => {
        this.detailTable = res.data.result;
      });
    },
    getListbyPage(page) {
			this.page = page;
			this.getList();
		},
  }
};
</script>

<style lang="scss" scoped>

</style>



